<template>
  <div class="board">
      <div class="boardheader">
          <span class="title">Storyboard {{ state }}</span>
      </div>
      <kanban-board :stages="statuses" :blocks="blocks" @update-block="updateBlock">
        <div v-for="item in blocks" :slot="item.id" :key="item.id">
            <CardSmall :card="item"></CardSmall>
        </div>
      </kanban-board>
  </div>
</template>

<script>
import faker from 'faker'
import { debounce } from 'lodash'
import CardSmall from './CardSmall'

export default {
  name: 'home',
  components: {
    CardSmall
  },
  data () {
    return {
      statuses: [''],
      blocks: [],
      state: "state"
    }
  },
  beforeCreate () {
    this.$http.get(`api/statuses`)
      .then(function (response) {
        this.statuses = response.data
      })
  },
  created () {
    this.$http.get(`api/cards`)
      .then(function (response) {
        var cards = response.data;
        for (let i = 0; i < cards.length; i += 1){
          this.blocks.push(cards[i])
          console.log(response.data)
          console.log(response.bodyText)
        }
      })
  },
  /*mounted () {
    for (let i = 3; i <= 10; i += 1) {
      this.blocks.push({
        id: i,
        status: this.statuses[Math.floor(Math.random() * 4)],
        title: faker.company.bs(),
        type: 'story',
        estimation: 2.5,
        users: [
          {
            id: 1,
            name: 'VLPV'
          },
          {
            id: 2,
            name: 'VZL'
          }
        ]
      })
    };
  },*/
  methods: {
    updateBlock: debounce(function (id, status) {
      this.blocks.find(b => b.id === Number(id)).status = status
      /*var card = {  id: id,  status: status };
      this.$http.post('api/update-card', card)
      .then(function (response) {
        this.state = response.data
      })*/
    }, 500)
  }
}
</script>

<style lang="scss">
@import './assets/board.scss';

* {
  box-sizing: border-box;
}
body {
  font-family: 'Lato';
  font-weight: 300;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

.board {
  margin: 2px;
}

.boardheader {
  border : 1px solid rgba(black, 0.4);
  background : rgba(black, 0.2);
  margin: 1px;
  height: 1cm;
  text-align: left;
   .title{
     color: black;
      font-size: 0.6cm;
      margin: 0.1cm;
    }
}

.board,
.boardheader {
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.drag-column {
    &-NONE,
    &-BACKLOG {
      .drag-column-header,
      .drag-options {
          border-top: 7px solid #741B47;
      }
    }

    &-ACCEPTED {
      .drag-column-header,
      .drag-options {
          border-top: 7px solid green;
      }
    }
}

.section {
  padding: 20px;
  text-align: center;
  a {
    color: white;
    text-decoration: none;
    font-weight: 300;
  }
  h4 {
    font-weight: 400;
    a {
      font-weight: 600;
    }
  }
}
</style>



