<template>
  <div class="home">
    <img src="./../assets/spring-boot-vuejs-logo.png">
    <h1>{{ msg }}</h1>
    <h2>This site contains more stuff :)</h2>
    <ul>
      <li>HowTo call REST-Services:</li>
      <li><a href="/#/callservice/" target="_blank">/callservice</a></li>
      <li>HowTo to play around with Bootstrap UI components:</li>
      <li><a href="/#/bootstrap/" target="_blank">/bootstrap</a></li>
      <li>Board</li>
      <li><a href="/#/board/" target="_blank">/board</a></li>
      <li>Router</li>
      <li>
        <router-link :to="{ name: 'Card', params: { id: 7 }}">
          card router
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to my first Vue.js powered Spring Boot App'
    }
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



