<template>
  <div>
    <h1>Hello! C-{{ $route.params.id }}</h1>
    <!--<h2>{{card.data.name}}</h2>-->
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Card',
  props: ['id'],
  data () {
    return {
      card: null
    }
  }/*,
  beforeRouteEnter (to, from, next) {
    // this.$http.get(`api/hello`)
    axios.get('/api/hello/')
      .then((card) => {
        next(vm => {
          vm.card = card
        })
      })
      .catch(() => {
        next('/404')
      })
  }*/
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }
</style>



